import React from "react"
import "hint.css"
import schedule from "../../schedule.json"
import {
  isThisISOYear,
  getISOYear,
  getISOWeek,
  isPast,
  isThisWeek,
  format,
  addDays,
} from "date-fns"

const thisYearsSchedule = schedule[getISOYear(new Date())]

const isBooked = week => {
  const scheduleWeek = thisYearsSchedule[getISOWeek(week)]
  return scheduleWeek && scheduleWeek.booked
}

const AVAILBLE_COLOR = "#50e59e"
const UNAVAILBLE_COLOR = "#dbdbdb"
const CURRENT_COLOR = "#9f3b29"

class Week extends React.Component {
  state = { isHovering: false }

  render() {
    const { week } = this.props
    const monday = format(week, "M/D")
    const friday = format(addDays(week, 4), "M/D")
    const popoverLabel = `${monday} - ${friday}`
    return (
      <div
        className="hint--top hint--rounded"
        aria-label={popoverLabel}
        css={{
          ...this.renderColors(),
          height: 60,
          borderRadius: 6,
          display: "flex !important",
          alignItems: "center",
          justifyContent: "center",
          fontSize: 20,
        }}>
        {format(week, "D")}
      </div>
    )
  }

  renderColors() {
    const { week } = this.props
    let backgroundColor = AVAILBLE_COLOR
    let color = "rgba(0,0,0,0.4)"
    if (isThisWeek(week)) {
      backgroundColor = CURRENT_COLOR
      color = "#fff"
    } else if (isPast(week) || isBooked(week)) {
      backgroundColor = UNAVAILBLE_COLOR
    }

    return { backgroundColor, color }
  }
}

export default Week



